// import service from '@/common/service.js';
import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '@/components/m-input.vue'

export default {
	components: {
		mInput
	},
	data() {
		return {
			hasProvider: true,
			showLogin:false,
			account: '', //账号
			password: '', //密码
			positionTop: 0,
			type: '', //certifica
			name: '',
			cardID: ''
		}
	},
	computed: mapState(['forcedLogin', 'userInfo']),
	onLoad(options) {
		let time = 1
		this.second = time + "s"
		this.time = setInterval(() => {
				if (time == 0) {
					clearInterval(this.time)
				
					this.showLogin=true


				} else {
					time--
					this.second = time + "s"
				}
			},
			1000)

	},
	onShow() {


	},
	onReady() {
		this.initPosition();
	},

	methods: {
		...mapMutations(['login']),

		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
			console.log(this.positionTop, '44');
		},
		onCertifica() {
			var regIdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/s
			if (this.name == '' || this.cardID == '') {
				uni.showToast({
					title: '信息不能为空',
					icon: "none"
				})
				return
			} else {
				if (!regIdNo.test(this.cardID)) {
					uni.showToast({
						title: '身份证号填写有误',
						icon: "none"
					})
					return
				}
			}
			uni.showToast({
				title: '提交成功',
				success: (res) => {
					uni.navigateBack({
						delta: 1
					})
				}
			})
		},
		bindLogin() {
			// uni.removeStorageSync('userInfos')
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			// if (this.account.length < 5) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '账号最短为 5 个字符'
			// 	});
			// 	return;
			// }
